
.targetcontent {
    background-color: #54ffa4;
    border: 2px solid white;
    padding: 4px 8px 4px 8px;
    margin-left: 10px;
    border-radius: 4px;
}

.nottargetcontent {
    background-color: #c5e0cd;
    border: 2px solid #b4b4b4;
    padding: 4px 8px 4px 8px;
    margin-left: 10px;
    border-radius: 4px;
}

#parent {
    bottom: 1em;
    z-index: 99;
    position: fixed;
    font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: 1em;
    color: #333333;
    left: 1em;
    display: flex;
    flex-flow: row nowrap;
}