#toggle {
    margin-left: 10px;
    background-color: #54ffa4;
    border: 2px solid white;
    padding: 4px 8px 4px 8px;
    border-radius: 4px;
    font-weight: bold;
    font-size: 1em;
    color: #333333;
    font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
}

#notoggle {
    margin-left: 10px;
    background-color: #c5e0cd;
    border: 2px solid #b4b4b4;
    padding: 4px 8px 4px 8px;
    border-radius: 4px;
    font-weight: bold;
    font-size: 1em;
    color: #333333;
    font-family: 'Helvetica Neue', Arial, Helvetica, sans-serif;
}