#toggles {
    position: fixed;
    top: 1em;
    left: 1em;
    display: flex;
    flex-flow: row nowrap;
    z-index: 99;
}

#risktoggles {
    position: fixed;
    top: 1em;
    right: 1em;
    display: flex;
    flex-flow: column nowrap;
    z-index: 99;
}

 .search-bar{
    position: absolute;
    right:0;
    font-family: sans-serif;
    font-size: 14px;
    color: #222;
    background-color: #fff;
    border-radius: 3px;
 }